"use client";
import { useEffect, useState } from "react";
import { Card } from "@/components/ui/card";
import {
  Home,
  Utensils,
  Car,
  Tv,
  ShoppingBag,
  MoreHorizontal,
} from "lucide-react";

// icons map
const iconMap = {
  Housing: <Home className="w-5 h-5 text-gray-500" />,
  Food: <Utensils className="w-5 h-5 text-gray-500" />,
  Transportation: <Car className="w-5 h-5 text-gray-500" />,
  Entertainment: <Tv className="w-5 h-5 text-gray-500" />,
  Shopping: <ShoppingBag className="w-5 h-5 text-gray-500" />,
  Others: <MoreHorizontal className="w-5 h-5 text-gray-500" />,
};

export default function ExpensesBreakdown({ userEmail = "test@gmail.com" }) {
  const [expenses, setExpenses] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [amount, setAmount] = useState("");

  // Fetch user expenses from backend
  useEffect(() => {
    if (!userEmail) return;
    fetch(`http://localhost:5000/api/expenses/${userEmail}`)
      .then((res) => res.json())
      .then((data) => setExpenses(data))
      .catch((err) => console.error("❌ Fetch error:", err));
  }, [userEmail]);

  // Add expense handler
  const handleAddExpense = async () => {
    if (!amount || !selectedCategory) return;

    const res = await fetch("http://localhost:5000/api/expenses/add", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        email: userEmail,
        category: selectedCategory.title,
        amount: Number(amount),
      }),
    });

    const updated = await res.json();
    setExpenses(updated); // refresh state
    setAmount("");
    setSelectedCategory(null); // close popup
  };

  return (
    <div className="w-full mt-6 lg:max-w-[680px] lg:ml-auto">
      {/* Heading */}
      <div className="flex justify-between items-center mb-3">
        <h2 className="text-22px font-inter text-gray-500 ml-3">
          Expenses Breakdown
        </h2>
        <span className="text-xs text-gray-400">*Compare to last month</span>
      </div>

      {/* Main Card */}
      <Card
        className="
          p-4 rounded-lg shadow-sm bg-white 
          w-full mt-2
          lg:max-w-[680px] lg:ml-auto
        "
      >
        {/* Grid Layout */}
        <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
          {expenses.map((item, idx) => (
            <div
              key={idx}
              onClick={() => setSelectedCategory(item)}
              className="flex flex-col gap-2 border rounded-lg p-3 hover:shadow transition cursor-pointer bg-white"
            >
              {/* Icon + Title */}
              <div className="flex items-center gap-2">
                {iconMap[item.title]}
                <span className="text-sm text-gray-600 font-medium">
                  {item.title}
                </span>
              </div>

              {/* Amount + Percent */}
              <div className="flex items-center justify-between">
                <span className="font-semibold text-gray-800">
                  ${item.amount.toFixed(2)}
                </span>
                <span
                  className={`text-xs font-medium ${
                    item.trend === "up" ? "text-red-500" : "text-green-600"
                  }`}
                >
                  {item.percent} {item.trend === "up" ? "↑" : "↓"}
                </span>
              </div>
            </div>
          ))}
        </div>
      </Card>

      {/* Popup */}
      {selectedCategory && (
        <div className="fixed inset-0 flex items-center justify-center bg-black/40 z-50">
          <div className="bg-white rounded-xl p-6 shadow-lg w-[90%] max-w-md">
            <h3 className="text-lg font-semibold mb-4">
              {selectedCategory.title} Expense
            </h3>

            <p className="mb-2 text-gray-600">
              Current: ${selectedCategory.amount.toFixed(2)}
            </p>

            <input
              type="number"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              placeholder="Enter amount"
              className="border rounded-lg w-full p-2 mb-4"
            />

            <div className="flex justify-end gap-2">
              <button
                onClick={() => setSelectedCategory(null)}
                className="px-4 py-2 rounded-lg border text-gray-600"
              >
                Cancel
              </button>
              <button
                onClick={handleAddExpense}
                className="px-4 py-2 rounded-lg bg-blue-600 text-white"
              >
                Add
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
